body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


h1, h2, h3, p, .text-field, label {
  padding: 5px;
  padding-left:10px;
}

md-outlined-text-field, md-filled-button, form {
  padding: 10px;
}

md-filled-button {
  left: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

md-filled-button.drill-button {
  left:100px;
}

.side-by-side {
    display: block;
    margin-right: 10px;
}
.cg-shapes {
  pointer-events: none;
  z-index: 10000;
  position: absolute;
  height: 288px;
  width: 288px;
  float: left;

  border-width: 1.75px;
  border-style: solid;
}

.pagination {
  padding:10px;
}

.pagination text-button {
  padding: 5px;
  cursor: pointer;
}

:host {
  --_container-color: var(--md-filled-button-container-color, var(--md-sys-color-primary, blue));
}

.currentPage {
  color: green;
}

md-outlined-select {
  padding: 10px;
}

